import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import './App.css';
import { LanguageProvider } from './contexts/LanguageContext';

// Import our enhanced components
import QueueInterface from './components/QueueInterface';
import EnhancedFlowRunner from './components/EnhancedFlowRunner';
import EnhancedAdminDashboard from './components/AdminDashboard';

// Import legacy components for backward compatibility
import UserKiosk from './pages/UserKiosk';
import AdminDashboard from './pages/AdminDashboard';
import DVROPage from './pages/DVROPage';
import DVFlowRunnerPage from './pages/DVFlowRunnerPage';

function App() {
  const [queueData, setQueueData] = useState(null);

  const handleQueueJoined = (data) => {
    setQueueData(data);
  };

  const handleProcessComplete = () => {
    setQueueData(null);
  };

  return (
    <LanguageProvider>
      <Router>
        <div className="App">
          <Routes>
            {/* Main user interface with enhanced queue system */}
            <Route path="/" element={
              queueData ? (
                <EnhancedFlowRunner 
                  queueNumber={queueData.queue_number}
                  caseType={queueData.case_type}
                  onComplete={handleProcessComplete}
                />
              ) : (
                <QueueInterface onQueueJoined={handleQueueJoined} />
              )
            } />
            
            {/* Enhanced admin dashboard */}
            <Route path="/admin" element={<EnhancedAdminDashboard />} />
            
            {/* Legacy routes for backward compatibility */}
            <Route path="/legacy" element={<UserKiosk />} />
            <Route path="/legacy-admin" element={<AdminDashboard />} />
            <Route path="/dvro" element={<DVROPage />} />
            <Route path="/dvro-flow" element={<DVFlowRunnerPage />} />
            
            {/* Demo route for testing enhanced system */}
            <Route path="/demo" element={
              <EnhancedFlowRunner 
                queueNumber="DEMO001"
                caseType={{ code: "DVRO", name: "Domestic Violence Restraining Order" }}
                onComplete={() => console.log("Demo completed")}
              />
            } />
            
            {/* Redirect any unknown routes to home */}
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
        </div>
      </Router>
    </LanguageProvider>
  );
}

export default App; 